import Vue from 'vue'
import Router from 'vue-router'

// 异步加载
// const My = resolve => require(['@/page/my'],resolve)
// const Schedule = resolve => require(['@/page/schedule'],resolve)

const Schedule = r => require.ensure([],() => r(require('@/page/schedule')),'group-schedule')
const My = r => require.ensure([],() => r(require('@/page/my')),'group-my')
const LookSchedule = r => require.ensure([],() => r(require('@/page/lookSchedule')),'group-lokSchedule')

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Schedule',
      component: Schedule
    },
    {
		path: '/my',
      	name: 'My',
      	component: My
    },
    {
      path:'/lookSchedule',
      name:'LookSchedule',
      component:LookSchedule
    }
  ]
})



